body{
  background-image: url(https://img.freepik.com/free-photo/3d-grunge-room-interior-with-spotlight-smoky-atmosphere-background_1048-11333.jpg?size=626&ext=jpg://images.assetsdelivery.com/compings_v2/nevodka/nevodka1305/nevodka130500041.jpg);
  
}
.App{
  background-color: deepskyblue;
  min-width: 500px;
  width: 500px;
  margin:60px auto;
}
#Just-a-form input{
  background-color: rgb(124, 132, 201);
  border: 0;
  width: 250px;
  height: 40px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 15px;
}
#Just-a-form input::placeholder{
  color: beige;
}
#Just-a-form input::focus,#Just-a-form button::focus{
  outline: none;
}
   
#Just-a-form button{
  background-color: darkkhaki;
  border: 0;
  width: 80px;
  height: 60px;
  cursor: pointer;
  color: aliceblue;
  font-weight: bolder;
  border-radius: 5px;
}


